"use client";

import React from "react";
import { useRouter } from "next/router";


const ModuleLayout = ({
  title,
  subtitle,
  videoUrl,
  description,
  resources,
  onExamClick,
}: {
  title: string;
  subtitle: string;
  videoUrl: string;
  description: string;
  resources: { title: string; link: string }[];
  onExamClick: () => void;
}) => {
  return (
    <div className="flex flex-col lg:flex-row lg:gap-6 p-6 bg-background text-foreground">
      <div className="w-full lg:w-2/3">
        <h1 className="text-3xl font-bold mb-2">{title}</h1>
        <h2 className="text-lg text-neutral-600 mb-4">{subtitle}</h2>
        <iframe
          className="w-full aspect-video rounded mb-6"
          src={videoUrl}
          title={title}
          allowFullScreen
        ></iframe>
        <p className="text-neutral-700">{description}</p>
      </div>
      <aside className="w-full lg:w-1/3 bg-background-1 p-4 rounded shadow">
        <button
          className="w-full bg-primary text-white py-2 rounded mb-4"
          onClick={onExamClick}
        >
          Presenta el test del m√≥dulo
        </button>
        <div>
          <h3 className="text-lg font-bold mb-2">Recursos</h3>
          <ul>
            {resources.map((resource, index) => (
              <li key={index} className="mb-2">
                <a
                  href={resource.link}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-primary underline"
                >
                  {resource.title}
                </a>
              </li>
            ))}
          </ul>
          <button
            onClick={() => router.push("/resources")}
            className="text-primary underline mt-4 block"
          >
            Ver todo
          </button>
        </div>
      </aside>
    </div>
  );
};

export default ModuleLayout;
